.result {
  max-width: 50rem;
  margin: 2rem auto;
  padding: 1rem;
  table-layout: fixed;
  border-spacing: 1rem;
  text-align: right;
  background-color: rgba(0, 0, 0, 0.5);
}

.result thead {
  font-size: 0.7rem;
  color: #436083;
}

.result tbody {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 0.85rem;
  color: #f7f7fb;
}
